import Footer from "../../components/Footer"
import Navbar from "../../components/Navbar"
import reset, { cleanupAll } from "../../utils/reset"
import Brands from "../../components/Brands"
import { trackPageVisit } from '../../utils/analtics'


const SECURITY_SECTIONS = [
    {
        title: "Platform Security",
        content: `
            <p class="text-gray-300">
                At Olymp AI Invest, we employ state-of-the-art security measures to protect your assets and personal information. Our multi-layered security infrastructure includes:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300 mt-4">
                <li>256-bit SSL encryption for all data transmission</li>
                <li>Two-factor authentication (2FA) for all accounts</li>
                <li>Multi-signature wallet technology</li>
                <li>Regular security audits by independent firms</li>
                <li>24/7 system monitoring and threat detection</li>
            </ul>
            <p class="text-gray-300 mt-4">
                We maintain strict security protocols that meet or exceed industry standards for financial institutions and cryptocurrency exchanges.
            </p>`
    },
    {
        title: "Asset Protection",
        content: `
            <p class="text-gray-300 mb-4">
                Your digital assets are protected through multiple security layers:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>95% of assets stored in air-gapped cold storage</li>
                <li>Hardware security modules (HSMs) for private key management</li>
                <li>Multi-signature authorization for all withdrawals</li>
                <li>Regular backup procedures with geographical distribution</li>
                <li>Insurance coverage against theft and cyber attacks</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Our cold storage solution uses military-grade encryption and requires multiple authorized signatures for any withdrawal, ensuring your assets remain secure even in the event of a security breach.
            </p>`
    },
    {
        title: "Account Security",
        content: `
            <p class="text-gray-300 mb-4">We implement multiple layers of account security:</p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>Mandatory two-factor authentication (2FA)</li>
                <li>Biometric authentication support</li>
                <li>IP address whitelisting</li>
                <li>Device verification</li>
                <li>Suspicious activity monitoring</li>
                <li>Account activity notifications</li>
                <li>Anti-phishing protection</li>
            </ul>
            <p class="text-gray-300 mt-4">
                We strongly recommend enabling all security features and regularly reviewing your account activity to maintain optimal security.
            </p>`
    },
    {
        title: "Regulatory Compliance",
        content: `
            <p class="text-gray-300">
                Olymp AI Invest maintains strict compliance with global regulatory standards:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300 mt-4">
                <li>AML (Anti-Money Laundering) procedures</li>
                <li>KYC (Know Your Customer) verification</li>
                <li>GDPR compliance for data protection</li>
                <li>Regular compliance audits</li>
                <li>Partnership with regulated custodians</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Our compliance team works continuously to ensure we meet or exceed all regulatory requirements in the jurisdictions where we operate.
            </p>`
    },
    {
        title: "Data Protection",
        content: `
            <p class="text-gray-300 mb-4">
                Your personal and financial data is protected through:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>End-to-end encryption for all communications</li>
                <li>Secure data centers with 24/7 surveillance</li>
                <li>Regular penetration testing</li>
                <li>Employee security training and background checks</li>
                <li>Strict access control policies</li>
            </ul>
            <p class="text-gray-300 mt-4">
                We employ industry-leading encryption standards and maintain strict internal policies regarding data access and handling.
            </p>`
    },
    {
        title: "Emergency Procedures",
        content: `
            <p class="text-gray-300">
                We maintain comprehensive emergency procedures for various scenarios:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300 mt-4">
                <li>24/7 incident response team</li>
                <li>Automated system lockdown protocols</li>
                <li>Regular disaster recovery drills</li>
                <li>Backup trading engines</li>
                <li>Multiple redundant systems</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Our emergency procedures are regularly tested and updated to ensure minimal disruption to our services in case of any security incidents.
            </p>`
    },
    {
        title: "Insurance Coverage",
        content: `
            <p class="text-gray-300 mb-4">
                Our platform is backed by comprehensive insurance coverage:
            </p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>Digital asset insurance up to $100 million</li>
                <li>Cyber security insurance</li>
                <li>Professional liability coverage</li>
                <li>Third-party custodian insurance</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Our insurance policies are maintained with leading providers and are regularly reviewed to ensure adequate coverage for our operations and client assets.
            </p>`
    },
    {
        title: "Security Best Practices",
        content: `
            <p class="text-gray-300 mb-4">We recommend following these security best practices:</p>
            <ul class="list-disc pl-6 space-y-3 text-gray-300">
                <li>Use a strong, unique password for your account</li>
                <li>Enable all available security features</li>
                <li>Never share your login credentials</li>
                <li>Use a secure internet connection</li>
                <li>Regularly monitor your account activity</li>
                <li>Keep your contact information updated</li>
                <li>Report any suspicious activity immediately</li>
            </ul>
            <p class="text-gray-300 mt-4">
                Following these practices helps ensure the security of your account and assets.
            </p>`
    },
    {
        title: "Contact Security Team",
        content: `
            <p class="text-gray-300">
                For security-related inquiries or to report suspicious activity:
            </p>
            <p class="text-gray-300 mt-4">
                Email: security@Olymp AI Invest.com<br>
                Emergency Hotline: +1 (800) 123-4567<br>
                Available 24/7/365
            </p>
            <p class="text-gray-300 mt-4">
                Our security team is available around the clock to assist with any security concerns or incidents.
            </p>`
    }
]

const security = async () => {
    reset('Olymp AI Invests Security')
    cleanupAll()
    await trackPageVisit()
    const { html: navbar, pageEvents: navEvents } = Navbar()

    function pageEvents() {
        navEvents()
    }

    return ({
        html: /* html */`
        ${navbar}
        <!-- Banner Section -->
        <section class="relative bg-cover bg-center py-20" 
                 style="background-image: url('https://softivus.com/wp/tradly/wp-content/uploads/2025/05/Breadcrumbs-Area-1-min.png')">
            <div class="absolute inset-0 bg-black/70"></div>
            <div class="container mx-auto px-4 relative z-10">
                <div class="text-center">
                    <h2 class="text-4xl md:text-5xl font-bold text-white mb-4">Security Measures</h2>
                    <nav aria-label="breadcrumb" class="flex justify-center">
                        <ol class="flex gap-2 text-gray-300">
                            <li><a href="/" data-nav class="hover:text-brand-primary">Home</a></li>
                            <li>&gt;</li>
                            <li class="text-brand-primary">Security</li>
                        </ol>
                    </nav>
                </div>
            </div>
        </section>

        <!-- Security Content -->
        <section class="py-20 bg-brand-dark">
            <div class="container mx-auto px-4">
                <div class="max-w-4xl mx-auto">
                    <div class="bg-brand-blue/10 rounded-2xl p-8 md:p-12 border border-brand-primary/20">
                        <!-- Header -->
                        <div class="text-center mb-12">
                            <h2 class="text-2xl md:text-3xl font-bold text-white">
                                Olymp AI Invest Security Policy
                            </h2>
                            <p class="text-gray-400 mt-4">
                                Last updated: August 21, 2025
                            </p>
                        </div>

                        <!-- Security Sections -->
                        <div class="space-y-12">
                            ${SECURITY_SECTIONS.map(section => `
                                <div class="security-section">
                                    <h3 class="text-xl font-bold text-brand-primary mb-6">
                                        ${section.title}
                                    </h3>
                                    <div class="space-y-4">
                                        ${section.content}
                                    </div>
                                </div>
                            `).join('')}
                        </div>

                        <!-- Footer Note -->
                        <div class="mt-12 pt-8 border-t border-brand-primary/20">
                            <p class="text-gray-400 text-sm">
                                We are committed to maintaining the highest standards of security and continuously updating our security measures to protect your assets and data. For any security concerns, please contact our security team immediately.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        ${Brands()}
        ${Footer().html}
        `,
        pageEvents
    })
}

export default security